.main-navigation {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 3.5rem;
    background: #3ea2ff;
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.main-navigation__logo h1{
    margin: 0;
    font-size: 1.5rem;
;
}

.main-navigation__items {
    margin-left: 1.5rem;
    padding: 0;
    list-style: none;
}

.main-navigation__items ul{
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    align-items: center;
}
.main-navigation__items li{

    margin: 0 1rem;
}
.main-navigation__items a,
.main-navigation__items button
{
    text-decoration: none;
    color: #fff73d;
    padding: 0.25rem 0.5rem;
    border: none;
    font: inherit;
    background: transparent;
    cursor: pointer;
}

.main-navigation__items a:hover,
.main-navigation__items button:hover,
.main-navigation__items a:active,
.main-navigation__items button:active,
.main-navigation__items a.active
{

    color: #fff;
}
