body {
  margin: 0;
  padding: 0;
 

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.form-control_ label,
.form-control_ input,
.form-control_ textarea{
  width: 100%;
  display: block;
}

.form-control_ label{
  margin-bottom: 0.5rem;
}

.form-control_ {
  margin-bottom: 1rem;
}

.form-action_ button,
.btn_ {
  background: #4268ff;
  font: inherit;
  border: 1px solid #4268ff;
  border-radius: 3px;
  padding: 0.25rem 1rem;
  margin-right: 1rem;
  color: white;
  cursor: pointer;
}

.form-action_ button:hover,
.form-action_ button:active,
.btn_:hover,
.btn_:active
{
  background:  #4268ff ;
  border-color: #4268ff ;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Eventdisabled {
  pointer-events: none;
  cursor: default;
}

 .footerStyle  {
  backgroundColor: purple;
  fontSize: 20px;
  color: white;
  borderTop: 1px solid #E7E7E7;
  textAlign: center;
  padding: 20px;
  position: fixed;
  left: 0;
  bottom: 0;
  height: 60px;
  width: 100%;
}

