 }
.auth-form_ {
    width: 40rem;
    max-width: 80%;
    margin: 5rem auto;
}

.form-background_ {
    width: 70%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 7rem auto;
    background: #9c9c9c;
    border-radius: 10px;
}
.spinner{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.lds-default {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.lds-default div {
    position: absolute;
    width: 5px;
    height: 5px;
    background: #d632ff;
    border-radius: 50%;
    -webkit-animation: lds-default 1.2s linear infinite;
            animation: lds-default 1.2s linear infinite;
}
.lds-default div:nth-child(1) {
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
    top: 29px;
    left: 53px;
}
.lds-default div:nth-child(2) {
    -webkit-animation-delay: -0.1s;
            animation-delay: -0.1s;
    top: 18px;
    left: 50px;
}
.lds-default div:nth-child(3) {
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
    top: 9px;
    left: 41px;
}
.lds-default div:nth-child(4) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
    top: 6px;
    left: 29px;
}
.lds-default div:nth-child(5) {
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
    top: 9px;
    left: 18px;
}
.lds-default div:nth-child(6) {
    -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
    top: 18px;
    left: 9px;
}
.lds-default div:nth-child(7) {
    -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;
    top: 29px;
    left: 6px;
}
.lds-default div:nth-child(8) {
    -webkit-animation-delay: -0.7s;
            animation-delay: -0.7s;
    top: 41px;
    left: 9px;
}
.lds-default div:nth-child(9) {
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
    top: 50px;
    left: 18px;
}
.lds-default div:nth-child(10) {
    -webkit-animation-delay: -0.9s;
            animation-delay: -0.9s;
    top: 53px;
    left: 29px;
}
.lds-default div:nth-child(11) {
    -webkit-animation-delay: -1s;
            animation-delay: -1s;
    top: 50px;
    left: 41px;
}
.lds-default div:nth-child(12) {
    -webkit-animation-delay: -1.1s;
            animation-delay: -1.1s;
    top: 41px;
    left: 50px;
}
@-webkit-keyframes lds-default {
    0%, 20%, 80%, 100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@keyframes lds-default {
    0%, 20%, 80%, 100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}


.event__list{
    margin-top: 2rem;
    max-width: 90%;
    margin: 2rem auto;
    list-style: none;
    padding: 0;
}

.events__list-item{
    margin : 1rem 0;
    padding: 1rem;
    border: 1px solid #4268ff;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.events__list-item h1{
    margin: 0;
    font-size: 1.5 rem;
    color: #4268ff;
}

.events__list-item h2{
    margin: 0;
    font-size: 1 rem;
    color: #e6e6e6;
}
.events__list-item p{
    margin: 0;
}
.modal_{
    width: 90%;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    position: fixed;
    top: 20vh;
    left: 5%;
    z-index: 100;
}

@media (min-width: 768px){
    .modal_ {
        width:30rem;
        left: calc((100% - 30rem) / 2) ;
    }
}

.modal__header_{
    padding: 1rem;
    background: #4268ff;
    color: white;

}
.modal__header_ h1{
    margin: 0;
    font-size: 1.25rem;
}

.modal__content_ {
    padding: 1rem;
}

.modal__actions_{
    display: flex;
    justify-content: flex-end;
    padding: 1rem;
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background: rgba(0,0,0, 0.75);
}
.events-control_ {
    text-align: center;
    border: 1px solid;
    padding: 1rem;
    margin: 2rem auto;
    width: 30rem;
    max-width: 80%;

}

.main-navigation {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 3.5rem;
    background: #3ea2ff;
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.main-navigation__logo h1{
    margin: 0;
    font-size: 1.5rem;
;
}

.main-navigation__items {
    margin-left: 1.5rem;
    padding: 0;
    list-style: none;
}

.main-navigation__items ul{
    display: flex;
    list-style: none;
    padding: 0;
    margin: 0;
    align-items: center;
}
.main-navigation__items li{

    margin: 0 1rem;
}
.main-navigation__items a,
.main-navigation__items button
{
    text-decoration: none;
    color: #fff73d;
    padding: 0.25rem 0.5rem;
    border: none;
    font: inherit;
    background: transparent;
    cursor: pointer;
}

.main-navigation__items a:hover,
.main-navigation__items button:hover,
.main-navigation__items a:active,
.main-navigation__items button:active,
.main-navigation__items a.active
{

    color: #fff;
}

